import "./Footer.css";
import linkdin from "../asists/ /Icon awesome-linkedin.png";
import facebook from "../asists/ /Icon awesome-facebook-square.png";
import whatsapp from "../asists/ /Icon awesome-whatsapp-square.png";
const Footer = (props) => {
  return (
    <section
      className="footer d-flex justify-content-center align-items-center"
      id="footer"
    >
      <p>Development Of Egyptian Team</p>
      <p>Connect Us</p>
      <div className="social__icons">
        <img src={facebook} alt="" />
        <img src={whatsapp} alt="" />
        <img src={linkdin} alt="" />
      </div>
    </section>
  );
};
export default Footer;
