import Container from "react-bootstrap/Container";
import Nav from "react-bootstrap/Nav";
import Navbar from "react-bootstrap/Navbar";
import NavDropdown from "react-bootstrap/NavDropdown";
import "./NavBar.css";
import logo from "../asists/img project/ .png";
import { Link } from "react-router-dom";
const NavBar = () => {
  return (
    <header>
      <Container className="d-flex justify-content-between ">
        <Link to="/">
          {" "}
          <div className="d-flex logo__container ">
            <h1 className="">
               <br />  
            </h1>
            <img src={logo} className="img-fluid" alt="" />
          </div>
        </Link>

        <Navbar bg="" expand="lg">
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto nav__container">
              <Nav.Link className="nav__link">
                <Link to="/"> </Link>
              </Nav.Link>

              <NavDropdown
                title=" "
                id="basic-nav-dropdown"
                className="nav__link"
              >
                <NavDropdown.Item>
                  <Link to="/hospitals"> </Link>
                </NavDropdown.Item>
                <NavDropdown.Item>
                  <Link to="/school"> </Link>
                </NavDropdown.Item>
                <NavDropdown.Item>
                  <Link to="/shops"> </Link>
                </NavDropdown.Item>
                <NavDropdown.Item>
                  <Link to="/industry"> </Link>
                </NavDropdown.Item>
                <NavDropdown.Item>
                  <Link to="/craft"> </Link>
                </NavDropdown.Item>
                <NavDropdown.Item>
                  <Link to="/elnady"> </Link>
                </NavDropdown.Item>
                <NavDropdown.Item>
                  <Link to="/ambulance"> </Link>
                </NavDropdown.Item>
              </NavDropdown>
              <Nav.Link className="nav__link">
                <Link to="/aboutvillage"> </Link>
              </Nav.Link>
              <Nav.Link className="nav__link">
                <Link to="/ratevillage">  </Link>
              </Nav.Link>
              <NavDropdown
                title="  "
                id="basic-nav-dropdown"
                className="nav__link"
              >
                <NavDropdown.Item href="#action/3.1">
                   
                </NavDropdown.Item>
                <NavDropdown.Item href="#action/3.2"> 2</NavDropdown.Item>
                <NavDropdown.Item href="#action/3.3"> 3</NavDropdown.Item>
              </NavDropdown>
              <Nav.Link className="nav__link">   </Nav.Link>
            </Nav>
          </Navbar.Collapse>
        </Navbar>
        <div className="location">
          <h2>
              
            <br />
              
          </h2>
        </div>
      </Container>
    </header>
  );
};
export default NavBar;
