import ServiceHeader from "../components/ServiceHeader";
import Tab from "react-bootstrap/Tab";
import Tabs from "react-bootstrap/Tabs";
import { Container } from "react-bootstrap";
import tennis from "../asists/ /Mask Group 30.png";
import "./Hospitals.css";
import Clinec from "../components/Clinec";
import { useState } from "react";
import DoctorCard from "../components/DoctorCard";
import doctorImg from "../asists/ /Img background.png";

const Hospitals = (props) => {
  const [clinecState, setCliencState] = useState({
    activeClince: null,
    clinecs: [
      { title: " ", img: tennis },
      { title: " ", img: tennis },
      { title: " ", img: tennis },
    ],
  });
  function activeHandler(index) {
    setCliencState({
      ...clinecState,
      activeClince: clinecState.clinecs[index],
    });
  }
  function addActiveHandler(index) {
    if (clinecState.clinecs[index] === clinecState.activeClince) {
      return "active";
    } else {
      return "ff";
    }
  }

  const doctorData = [
    {
      img: doctorImg,
      name: "  ",
      specialty: "   ",
      specialtyName: " ",
      scanState: " ",
      place: "   ",
    },
    {
      img: doctorImg,
      name: "  ",
      specialty: "   ",
      specialtyName: " ",
      scanState: " ",
      place: "   ",
    },
    {
      img: doctorImg,
      name: "  ",
      specialty: "   ",
      specialtyName: " ",
      scanState: " ",
      place: "   ",
    },
  ];
  return (
    <>
      <ServiceHeader title="" />
      <Container>
        <Tabs
          defaultActiveKey="home"
          id="uncontrolled-tab-example"
          className="mb-3"
        >
          <Tab eventKey="home" title="">
            <div className="elnady hospital">
              <div className="sports__holder d-flex justify-content-between flex-wrap">
                {clinecState.clinecs.map((cl, i) => (
                  <Clinec
                    key={i}
                    title={cl.title}
                    img={cl.img}
                    i={i}
                    onClick={activeHandler}
                    className={addActiveHandler(i)}
                  />
                ))}
              </div>
              <div className="doctors d-flex justify-content-center flex-wrap">
                {doctorData.map((doctor, i) => {
                  if (clinecState.activeClince == null) {
                    return (
                      <DoctorCard
                        key={i}
                        img={doctor.img}
                        name={doctor.name}
                        specialty={doctor.specialty}
                        specialtyName={doctor.specialtyName}
                        scanState={doctor.scanState}
                        place={doctor.place}
                      />
                    );
                  }
                  if (clinecState.activeClince.title === doctor.specialtyName) {
                    return (
                      <DoctorCard
                        key={i}
                        img={doctor.img}
                        name={doctor.name}
                        specialty={doctor.specialty}
                        specialtyName={doctor.specialtyName}
                        scanState={doctor.scanState}
                        place={doctor.place}
                      />
                    );
                  }
                })}
              </div>
            </div>
          </Tab>
          <Tab eventKey="profile" title=" ">
            <div className="d-flex justify-content-center align-items-center">
              <p className="elnady__p">    </p>
            </div>
          </Tab>
          <Tab eventKey="contact" title=" ">
            <div className="d-flex justify-content-center align-items-center">
              <p className="elnady__p">   </p>
            </div>
          </Tab>
          <Tab
            eventKey="save"
            title="  (  )"
            disabled
          >
            <div className="d-flex justify-content-center align-items-center">
              <p className="elnady__p">   </p>
            </div>
          </Tab>
        </Tabs>
      </Container>
    </>
  );
};
export default Hospitals;
