import React from "react";
import craft1 from "../asists/ /Mask Group 19.png";
import craft2 from "../asists/ /Mask Group 26.png";
import craft3 from "../asists/ /Mask Group 20.png";
import craft4 from "../asists/ /Mask Group 21.png";
import craft5 from "../asists/ /Mask Group 24.png";
import craft6 from "../asists/ /Mask Group 25.png";
import craft7 from "../asists/ /Mask Group 23.png";
import ServiceHeader from "../components/ServiceHeader";
import Clinec from "../components/Clinec";
import { useState } from "react";

function LinkOfCrafts() {
  const [clinecState, setCliencState] = useState({
    activeClince: null,
    clinecs: [
      { title: "", img: craft1 },
      { title: " ", img: craft2 },
      { title: " ", img: craft3 },
      { title: "", img: craft4 },
      { title: "", img: craft5 },
      { title: "", img: craft6 },
      { title: "", img: craft7 },
    ],
  });
  function activeHandler(index) {
    setCliencState({
      ...clinecState,
      activeClince: clinecState.clinecs[index],
    });
  }
  function addActiveHandler(index) {
    if (clinecState.clinecs[index] === clinecState.activeClince) {
      return "active";
    } else {
      return "ff";
    }
  }

  const doctorData = [
    {
      name: "  ",
      specialty: "   ",
      specialtyName: " ",
      scanState: " ",
      place: "   ",
    },
    {
      name: "  ",
      specialty: "   ",
      specialtyName: " ",
      scanState: " ",
      place: "   ",
    },
    {
      name: "  ",
      specialty: "   ",
      specialtyName: " ",
      scanState: " ",
      place: "   ",
    },
  ];
  return (
    <>
      <ServiceHeader title=" " />
      <section className="container">
        <div className="elnady hospital">
          <div className="sports__holder d-flex justify-content-between flex-wrap">
            {clinecState.clinecs.map((cl, i) => (
              <Clinec
                key={i}
                title={cl.title}
                img={cl.img}
                i={i}
                onClick={activeHandler}
                className={addActiveHandler(i)}
              />
            ))}
          </div>
          <div className="doctors d-flex justify-content-center flex-wrap">
            {doctorData.map((doctor, i) => {
              if (clinecState.activeClince == null) {
                return <div></div>;
              }
              if (clinecState.activeClince.title === doctor.specialtyName) {
                return <div>he</div>;
              }
            })}
          </div>
        </div>

        <hr className="mb-5" />
      </section>
    </>
  );
}

export default LinkOfCrafts;
